import Link from "next/link";
import { Badge } from "@/components/ui/badge";
import cases from "@/data/mock_cases.json";
import { CaseSchema } from "@/lib/schema";

export default async function CasesPage() {
  const parsed = (cases as unknown[]).map((c) => CaseSchema.parse(c));

  return (
    <div className="p-6 space-y-4">
      <h1 className="text-2xl font-semibold">HITL Review Inbox</h1>

      <div className="border rounded-lg overflow-hidden">
        <table className="w-full text-sm">
          <thead className="bg-muted">
            <tr className="text-left">
              <th className="p-3">Case ID</th>
              <th className="p-3">User</th>
              <th className="p-3">Tool</th>
              <th className="p-3">Verdict</th>
              <th className="p-3">Score</th>
              <th className="p-3">Status</th>
            </tr>
          </thead>
          <tbody>
            {parsed.map((c: any) => (
              <tr key={c.id} className="border-t hover:bg-muted/50">
                <td className="p-3">
                  <Link className="underline" href={`/cases/${c.id}`}>
                    {c.id}
                  </Link>
                </td>
                <td className="p-3">{c.user_display}</td>
                <td className="p-3">{c.tool_name}</td>
                <td className="p-3">
                  <Badge>{c.risk_label}</Badge>
                </td>
                <td className="p-3">{c.risk_score}</td>
                <td className="p-3">{c.status}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
